<template>
    <div class="contact-card">
        <div class="icon">
            <img :src="resolvedIcon" alt="icon" />
            <h2>{{ title }}</h2>
        </div>
        <div class="content">
            <div class="block">
                <span class="phone-icon"></span>
                <p>{{ phone }}</p>
            </div>
            <div class="block" v-if="gsm">
                <span class="gsm-icon"></span>
                <p>{{ gsm }}</p>
            </div>
            <div class="block">
                <span class="mail-icon"></span>
                <p><a :href="`mailto:${mail}`">{{ mail }}</a></p>
            </div>
            <div class="block">
                <span class="adress-icon"></span>
                <p>{{ adress }}</p>
            </div>
        </div>
    </div>
</template>


<script>
export default {
    name: "ContactCard",
    props: {
        title: {
            type: String,
            required: true
        },
        icon: {
            type: String,
            required: true
        },
        phone: {
            type: String,
            required: true
        },
        gsm: {
            type: String
        },
        mail: {
            type: String,
            required: true
        },
        adress: {
            type: String,
            required: true
        }
    },
    computed: {
        resolvedIcon() {
            // Use require to resolve the icon path statically
            try {
                return require(`@/assets/icons/${this.icon}`);
            } catch (error) {
                console.error("Icon not found:", error);
                return ""; // Return a fallback or empty string if the icon is not found
            }
        }
    }
};
</script>

