                                <router-link to="/industries/energy/dogusan-milas-shell" class="fore-text">Dousan Milas Shell</router-link>
                                <router-link to="/industries/energy/maviliman-milas-shell" class="fore-text">Maviliman Milas Shell</router-link>
                                <router-link to="/industries/energy/saltan-petrol-sirnak-opet" class="fore-text">Saltan Petrol rnak Opet</router-link>
